.resetBtn {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin: 0 auto;
    bottom: 20px;
    overflow: visible;
    overflow: initial;
}

@media screen and (max-width: 768px) {
    nav p {
        font-size: 3rem !important;
    }
    .nav-wrapper {
        padding: 1rem 2rem 0px !important;
    }
}

@media screen and (max-width: 576px) {
    nav p {
        font-size: 3.0rem !important;
        bottom: 12%;
    }
    .nav-wrapper {
        padding: 1rem 0 0px !important;
    }
    .scoreBtn {
        width: 36px !important;
        height: 36px !important;
        line-height: 36px !important;
        font-size: 11px !important;
    }
    .resetBtn {
        bottom: 0
    }
}

@media screen and (max-width: 375px) {
    nav p {
        font-size: 1.4rem !important;
        top: -8px;
    }
    .resetBtn {
        bottom: -8px !important;
    }
}
.wrapper {
  min-height: calc(100vh - 100px);
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
  align-content: flex-start;
  overflow: auto;
  margin: 0 auto;
  padding: 0 40px 100px;
}

.cardHolderDiv {
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
}

.card {
  border-radius: 10px;
  overflow: hidden;
  max-height: 140px;
  position: relative;
  /* box-shadow: 0 3px 6px #999, 0 3px 6px #999; */
  width: 200px;
  margin: 0 5px 5px;
}

.card > .remove {
  color: #ff1744;
  font-size: 48px;
  position: absolute;
  right: 0;
  font-size: 2rem;
  cursor: pointer;
}

.card > .remove:hover {
  color: #d50000;
}

.disabled {
  display: none
}

@media screen and (max-width: 768px) {
  .card {
    height: 122px !important;
    width: 22%;   
  }
}

@media screen and (max-width: 576px) {
  .card {
    height: 88px !important;
    width: 18%;   
  }

  .friendImg {
    padding-top: 28px !important;
  }

  .card span {
    padding: 10px !important;
    font-size: 0.5rem !important;
  }
}
button {
  cursor: pointer;
